.App {
  text-align: center;
  color: white;
}

.App-nav-fixed-container-k {
  position: block;
  width: 100%;
  z-index: 3;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #0E97FF;
  height: 60px;-webkit-box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.1); 
  box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.1);
}
.App-nav-logo {
  height: 50px;
}

.App-menu-on {
  display: block;
}

.App-menu-off {
  display: none;
}

.App-menu-button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}

.App-nav-bar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #0E97FF;
  -webkit-box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.03); 
  box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.03);
}

.App-nav-bar button {
  border: none;
  padding: 10px;
  background-color: unset;
  color: white;
  font-size: 1em;
}

#menu_active {
  font-weight: 900;
}

#menu_inactive {
  font-weight: 600;
}