img {
    height: 50px;
}

.Home-intro-1 {
    background-color: #0E97FF;
    padding: 20px 15px 70px 20px;
}

.Home-intro-1-content h1 {
    color: white;
    font-size: 1.8em;
}

.Home-intro-1-content h3 {
    color: white;
    font-size: 1.2em;
    font-weight: 300;
}

.Home-intro-2 {
    padding: 20px 15px;
    background-color: rgba(0, 0, 0, 0.07);
}

.Home-intro-2-up {
    position: relative;
    top: -70px;
    background-color: white;
    border-radius: 20px;
    padding: 20px 20px;
    display: flex;
    flex-direction: row;
}

.Home-intro-2-up img {
    padding-right: 7px;
    padding-top: 20px;
}

.Home-intro-2-up-download {
    padding-bottom: 19px;
}

.Home-intro-2-up-download button {
    background-color: #0E97FF;
    border-radius: 5px;
    border: none;
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 10px 0px;
    padding: 10px 10px;
    color: white;
    font-size: 0.9em;
    font-weight: 800;
}

.Home-intro-2-up-download button:hover {
    border: #0E97FF solid 1px;
    background-color: white;
    color: #0E97FF;
}

.Home-intro-2-content {
    margin-top: -70px;
}

.Home-intro-2-content h1 {
    text-align: center;
    font-weight: 200;
    font-size: 3em;
    color: rgba(36, 48, 201, 1);
}

.Home-intro-3-content-script h3 {
    font-weight: 400;
}